.main-navigation {
    display: block;
    width: 100%;
    align-items: center;

    .menu-toggle {
        font-size: 0;
        width: 40px;
        height: 40px;
        background-color: transparent;
        border: none;
        cursor: pointer;

        // middle line
        position: relative;
        background: linear-gradient(white, white) no-repeat center;
        background-size: 30px 4px;

        &::before,
        &::after {
            content: "";
            display: block;
            width: 30px;
            height: 4px;
            background-color: white;
            margin: 8px auto;
            transition: all 0.3s;
        }
        &::after {
            content: "";
        }
    }

    ul {
        display: none;
        list-style: none;
        margin: 0;
        padding-left: 0;

        ul {
            box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
            float: left;
            position: absolute;
            top: 100%;
            left: -999em;
            z-index: 99999;

            ul {
                left: -999em;
                top: 0;
            }

            li {
                &:hover > ul,
                &.focus > ul {
                    display: block;
                    left: auto;
                }
            }

            a {
                width: 200px;
            }

            :hover > a,
            .focus > a {
            }

            a:hover,
            a.focus {
            }
        }

        li:hover > ul,
        li.focus > ul {
            left: auto;
        }
    }

    li {
        position: relative;

        &:hover > a,
        &.focus > a {
        }
    }

    a {
        display: block;
        text-decoration: none;
    }

    .current_page_item > a,
    .current-menu-item > a,
    .current_page_ancestor > a,
    .current-menu-ancestor > a {
    }
}

.toggled .nav-menu {
    background-color: black;
    position: absolute;
    top: 7.5rem;
	left: 0;
	right: 0;
	text-align: center;
	z-index: 10;

    a {
		padding: 1rem;
    }
}

.nav-menu {
    gap: 3rem;

	a {
        text-decoration: none;
        color: white;
        font-weight: 500;
        text-transform: uppercase;

        &:visited {
            color: white;
        }

        &:hover,
        &:focus {
            background-color: #454545;
        }
    }
}

/* Small menu. */
.menu-toggle,
.main-navigation.toggled ul {
    display: block;
}

@media screen and (min-width: 50em) {

	.menu-navigation-container {
		// position:initial
	}

    .menu-toggle {
        display: none;
    }

    .main-navigation ul {
        display: flex;
    }
}

.comment-navigation,
.posts-navigation,
.post-navigation {
    .site-main & {
        margin: 0 0 1.5em;
    }

    .nav-links {
        display: flex;
    }

    .nav-previous {
        flex: 1 0 50%;
    }

    .nav-next {
        text-align: end;
        flex: 1 0 50%;
    }
}
